import React from "react";

function ListItems(props){
    const items = props.items;
    const listItems = items.map(item =>
   {
       return <div key={item.key}>
     <p>
         <input type="text" id={item.key} value={item.item}></input>
                    </p>

                </div>
            
         } 
    )
        
    return (
        <div>{listItems}</div>
    )
    }
        
    
export default ListItems