import React from "react";
import { Component } from "react";
import Additem from "../additems/additem";
import "./todolist.css" ;
import Listitems from "../additems/listitems";


export default class Todolist extends Component {
    constructor(props){
        super(props);

    }
    state = {
       Items :[] ,
       currrentItem : {
           item :'' ,
           key :''
       }
    }
        
    handleinput = (e) => {
             this.setState
         (
            {currrentItem : {
            item : e.target.value ,
            key : Date.now() }
            }) ;
     
    };


    handlesubmit = (e) => {
        e.preventDefault(); //to prevent refresh of Page 
        const newitem = this.state.currrentItem ;
        console.log (newitem);
        if (newitem !== ""){
            const newItems = [...this.state.Items , newitem];
            this.setState( {Items : newItems})
        }
        
    };
       
        render () {
            return (
        
        <div className = "form" >
        <form onSubmit ={this.handlesubmit}>
    <input       id="text"    
                type= "text"
                 value ={this.state.currrentItem.item}
                onChange={this.handleinput}
                placeholder= "ToDo....."/>
        <button type="submit" >Add</button>
        </form>
      
                <div>{this.state.Items.item}</div>

                <Listitems Items={this.state.Items}></Listitems>
        </div>
   
    
            )  }
}